<template>
    <van-grid :column-num="5" :border=false>
  <van-grid-item
    border=false
    v-for="(value,idx) in tabs"
    :key="idx"
    :to="`/detail/${idx}`"
  >
  <van-image :src="value.url" />
  <span>
      {{value.name}}
  </span>
  </van-grid-item>
</van-grid>
</template>
<script>
export default {
    data(){
        return {
            tabs:[]
        }
    },
    methods:{
    },
    async mounted(){
        let msg = await this.getData("get","https://www.easy-mock.com/mock/5d403de5bf069f5f10cf353f/example/list");
        this.tabs = msg;
    }
}
</script>
<style>
    span{
        font-size: 12px;
    }
</style>
