import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter);

import Detail from '../page/Detail'
import Shouye from '../page/Shouye'

const routes = [
    //重定向路由
  { path: '/', redirect: { name: 'shouye' }},
  { path: '/shouye', redirect: { name: 'main' }},
    //组件大写，其余命名均为小写
    //动态路由匹配
    //{ path: '/detail/:id', name:'detail', component: Detail },
      //接下来通过props来解耦$route
  { path: '/detail/:id',
    name:'detail',
    component: Detail ,
    props:true,

   },
  { path: '/shouye',
   name:'shouye' , 
   component: Shouye ,
   children:[
    {
        // 当 /shouye/discover 匹配成功，
        // Discover 会被渲染在 User 的 <router-view> 中
        path: 'main',
        name: 'main',
        component: Shouye.components.Main
    },{
      path: 'wode',
        name: 'wode',
        component: Shouye.components.Wode
    },{
      path: 'login',
        name: 'login',
        component: Shouye.components.Login
    },{
      path: 'label',
        name: 'label',
        component: Shouye.components.Label
    }
   ] },
]

const router = new VueRouter({
  routes // (缩写) 相当于 routes: routes
})
