import axios from 'axios'
router.beforeEach(async (to,from,next)=>{
 let data = await axios.post('https://www.easy-mock.com/mock/5d419c967118b96594f4498e/list/login',{
    params:{
      //存在cookie里面
      //用token代替账号密码
      token :'asdsadasdas'
    }
  })
  let isLogin = data.data.data.status
  //如果没有登录就进sign
  //如果登录就next
  //如果要去登录页也可以next
  //如果是首页，详情页，登录页或者登陆了，都可以进去，否则不给进
  if( isLogin||to.name==='main'||to.name==='login'||to.name==='shouye'||from.path==='/'){
    next()
  }else{
    //编程式导航
    router.push({
      name:'login'
    })
  }
})
export default router