<template>
  <div class="search">
    <div class="address">
      <span class="van-dropdown-menu__title arrow"><van-icon name="location" />慧通产业园B9栋</span>
      <van-search :class="navBarFixed == true ? 'navBarWrap' :''" placeholder="搜索饿了么商家、商品名称" v-model="value" background="#0085ff" />
    </div>
    
  </div>
</template>
<script>
export default {
  data(){
    return {
      value:'',
      navBarFixed:false
    }
  },
  methods:{
    watchScroll () {
        var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
        //  当滚动超过 50 时，实现吸顶效果
        if (scrollTop > 49) {
          this.navBarFixed = true
        } else {
          this.navBarFixed = false
        }
     }
  },
  mounted () {
      // 事件监听滚动条
      window.addEventListener('scroll', this.watchScroll)
    }
};
</script>
<style scoped>
  .address{
    background:#0085ff;
    color: white;
    font-size: 20px;
    line-height: 40px;
    text-align: left;
    
  }
  span{
    padding-left: 20px
  }
  .navBarWrap {
    position:fixed;
    top:0;
    z-index:999;
    left: 0;
    right: 0;
  }
  span.van-dropdown-menu__title.arrow::after{
    top:4px;
  }
</style>
