<template>
  <div class="xiala top" v-if="bool">
    <div @click="top">
      <van-dropdown-menu >
        <van-dropdown-item v-model="value1" :options="option1" />
      </van-dropdown-menu>
    </div>

    <div>距离最近</div>
    <div>品质联盟</div>
    <div>筛选</div>
  </div>
  <div class="xiala" v-else>
    <div @click="top" class="van-dropdown-menu__title">
      <div>{{option1[this.value1].text}}</div>
    </div>

    <div>距离最近</div>
    <div>品质联盟</div>
    <div>筛选</div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      bool:false,
      value1: 0,
      option1: [
        { text: "综合排序", value: 0 },
        { text: "好评优先", value: 1 },
        { text: "销量最高", value: 2 },
        { text: "起送价最低", value: 3 },
        { text: "配送最快", value: 4 },
        { text: "起送价最低", value: 5 },
        { text: "配送最快", value: 6 },
        { text: "配送费最低", value: 7 },
        { text: "人均从低到高", value: 8 },
        { text: "人均从高到低", value: 9 },
        { text: "通用排序", value: 10 },
      ]
    }
  },
  mounted(){
      window.addEventListener('click',(e)=>{
          if(e.target.className==="van-overlay van-fade-leave van-fade-leave-active"){
              this.bool=false;
          }
          if(e.target.className==="van-cell__title"){
              this.bool=false;
          }
      })
  },
  methods:{
    top(){
        this.bool=true;
    } 
  }
};
</script>
<style>
.xiala {
    background:white;
  display: flex;
  justify-content: space-around;
  line-height: 50px;
}
.van-dropdown-menu__title::after{
    top:19px;
}
.top{
    position:fixed;
    top:94px;
    z-index:999;
    left: 0;
    right: 0;
}
</style>
